@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;600&family=Roboto:wght@400;700&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  font-family: 'Open Sans', sans-serif;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


.login-form{
    position: absolute;
    max-width: 30rem;
    min-height: 20rem;
    margin: 5rem auto;
    border: none;
    border-radius: 0.5rem;
    padding: 2rem;
    box-shadow: 2px 2px 5px 5px rgba(0,0,0,0.3);
    background-color: #fff;
}

.form{
    margin: 2rem auto;
}

.form .input{
    border: none;
    border-bottom: 2px solid rgba(0,0,0,0.3);
}

.form .input:focus{
    outline: none !important;
    box-shadow: none!important;
}

p{
    font-weight: 600;
}

h4{
    text-align: center;
    text-shadow: 2px 2px 2px rgba(0,0,0,0.3);
    opacity: 0.8;
}

span{
    color: #4285F4;
    margin-right: 5px;
}

.sub-btn:active{
    outline: none !important;
    box-shadow: none !important;
}
.main{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-image:linear-gradient(to right, rgb(125, 144, 150), #839b5b);
    background-size: cover;;
}

.register-form{
    position: absolute;
    max-width: 30rem;
    min-height: 20rem;
    margin: 5rem auto;
    border: none;
    border-radius: 0.5rem;
    padding: 2rem;
    box-shadow: 2px 2px 5px 5px rgba(0,0,0,0.3);
    background-color: #fff;
}

.form{
    margin: 2rem auto;
}

.form .input{
    border: none;
    border-bottom: 2px solid rgba(0,0,0,0.3);
}

.form .input:focus{
    outline: none !important;
    box-shadow: none!important;
}

p{
    font-weight: 600;
}

.sub-btn:active{
    outline: none !important;
    box-shadow: none !important;
}

.nav-right{
    margin-right: 1rem;
    margin-left: auto;
}
.post-main{
    position: relative;
    width: 100%;
    min-height: 10rem;
    margin-bottom: 2rem;
    border: none;
    border-radius: 5px;
    box-shadow: 2px 2px 2px 2px rgba(0,0,0,0.3);
}

.post-main .post-top{
    width: 100%;
    display: flex;
    justify-content: space-between;
    height: 3rem;
}

.post-main .post-top img{
    width:2rem;
    height: 2rem;
    border-radius: 50%;
    margin: auto 1rem auto;
}

.post-main .post-top p{
    margin:auto 1rem auto;
}

.post-main .img-container{
    width: 100%;
}

.post-main .img-container img{
    width: 100%;
    height: 25rem;
}

.post-main .like{
    position: relative;
    width: 100%;
    height: 2rem;
    display: flex;
    justify-content: flex-end;
}

.post-main .like .like-sub1,.like-sub2{
   margin-right: 0.5rem;
}

.post-main .content{
    width: 100%;

}

.post-main .content p{
    font-weight: 400;
    font-style: italic;
    
}

.post-main .comment{
    width: 100%;
}

.post-main .comment .comment-gp{
    display: flex;
    padding: 5px;
}

.post-main .comment .comment-gp #comment{
    margin-right: 1rem;
    border: none;
    border-right: 2px solid rgba(0,0,0,0.3);
    border-bottom: 2px solid rgba(0,0,0,0.3);
}

.post-main .comment .comment-gp #comment:focus{
    outline: none!important;
    box-shadow: none!important;
}

.post-main .comment .comment-gp #btn:focus{
    outline: none!important;
    box-shadow: none!important;
}

.sidebar-main{
    margin-top: 2rem;
    margin-left: 3rem;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.sidebar-main h3{
    text-align: center;
    text-shadow: 3px 3px 3px rgba(0, 0, 0, 0.3);
}

.sidebar-main .card img{
    width: 50px;
    height: 50px;
    border-radius: 50%;
}

.sidebar-main .card{
    position: relative;
    margin-bottom: 1rem;
    width: 15rem;
    height: 5rem;
}

.sidebar-main .card .card-online{
 position: absolute;
 width: 10px;
 height: 10px;
 background-color: green;
 border-radius: 50%;
 top: 2px;
 right: 2px;
}

.sidebar-main .card .card-sub{
    display: flex;
    justify-content: space-between;
}


.topbar-main{
    width: 100%;
    min-height: 10rem;
    margin-top: 2rem;
    
}

.topbar-form{
    width: 100%;
    padding: 1rem;
    display: flex;
    border: 0;
    border-radius: 5px;
    box-shadow: 2px 2px 2px 2px rgba(0,0,0,0.3);
}

.topbar-form-group1{
    display: flex;
    flex-direction: column;
    flex: 1 1;
}

.topbar-form-group1 #text{
    margin-bottom: 1rem;
}

.topbar-form-group1 #text:focus{
    outline: none !important;
    box-shadow: none !important;
}

.topbar-form-group1 #post{
    height: 2rem;
}

.topbar-form-group1 #post:focus{
    outline: none !important;
    box-shadow: none !important;
}


.topbar-form-group2{
    flex: 0.5 1;
}

.topbar-form-group2 #btn{
    margin-left: 2rem;
    width: 8rem;
    cursor: pointer;
}

.topbar-form-group2 #btn:focus{
    outline: none !important;
    box-shadow: none !important;
}


.home-main{
   max-width: 100vw;
}

.home-main .home-container{
    margin-left: 0;
}
.banner-main{
    width: 100%;
    min-height: 60vh;
    position: relative;
}

.cover-img-container{
   width: 100%;
   height: 420px;
}

.cover-img{
    width: 100%;
    height: 420px;
    opacity: 0.7;
}

.profile-img-container{
    position: absolute;
    width: 15rem;
    height: 15rem;
    top: 10rem;
    left:40%;
    border-radius: 50%;
    box-shadow: 2px 2px 2px 3px rgba(0,0,0,0.3);
}

.profile-img{
    width:100%;
    height: 100%;
    border-radius: 50%;
    background-size: cover;
}

.banner-details{
    width: 100%;
    min-height: 30vh;
    background-color: rgb(188, 200, 201);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
   
}

.banner-details .banner-details-top{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.banner-details .banner-details-top p{
    margin: 0 5px;
}

.banner-btn-container{
    display: flex;
}


.banner-btn{
  margin:5px 5px;
}
.banner-btn:focus{
    outline: none !important;
    box-shadow: none !important;
}

.banner-description{
 margin-top: 1rem;
 padding: 5px;
 line-height: 1px;
}

.banner-description .banner-form{
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.banner-description .banner-btn{
    margin-left: 5px;
    width: 5rem;
}




.modal-form{
    width: 100%;
    padding: 1rem;
    display: flex;
    border: 0;
    border-radius: 5px;
    box-shadow: 1px 1px 1px 1px rgba(0,0,0,0.3);
}

.modal-form-group1{
    display: flex;
    flex-direction: column;
    flex: 1 1;
}


.view-post-main{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 2rem;
}
.comments-main{
    width: 100%;
    min-height: 10rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: none;
    border-radius: 5px;
    box-shadow: 2px 2px 2px #ccc;
}

.comments-main .card{
    position: relative;
    margin-bottom: 1rem;
    width: 90%;
    min-height: 2rem;
}

.comments-main .card img{
    width: 25px;
    height: 25px;
    border-radius: 50%;
}

.comments-main .card .card-sub{
    display: flex;
    justify-content: space-between;
}

.comments-main p{
    margin-top: 1rem;
    margin-bottom: 5px;
    font-size: 15px;
    font-weight:  normal;
    font-family: 'Roboto', sans-serif;
}


.view-container{
    margin-top: 2rem;
}
